.navHolder {
  background-color: #ec4c8a;
  max-width: 100%;
  height: 8vh;
  margin: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0 -20px 5px 20px black;
  box-sizing: border-box;
}

.backKey {
  width: 10%;
  padding-left: 10px;
}

svg {
  color: white;
  cursor: pointer;
}

.backKey > svg {
  font-weight: bolder;
  width: 50%;
  color: white;
}

.navTitle {
  width: 70%;
  text-align: center;
  font-weight: 400;
  font-size: 1rem;
  color: white;
}

.iconHolder {
  display: flex;
  justify-content: space-between;
  width: 16%;
  margin-right: 15px;
}
