.homepageHolder {
  max-width: 100%;
  min-height: 92vh;
}

.homepageImage {
  position: relative;
  overflow: hidden;
  max-height: 38vh;
}

.gradientLayer {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to right, rgba(0, 0, 0, 0.548), rgba(236, 76, 137, 0.774));
}

.earthfull {
  width: 100%;
  height: auto;
  max-height: 100%;
}

.restHome {
  display: block;
  background-color: #ec4c8a;
  max-width: 100%;
  min-height: calc(100vh - 38vh);
}

.homeRestHeader {
  font-size: 0.8rem;
  font-weight: 400;
  width: 100%;
  padding-top: 10px;
  position: relative;
  top: 10px;
  left: 10px;
}
